import React from 'react';
import { Row, Col } from 'antd';
import ProgressBar from '../../Progress';

const SkillsProgress = () => (
  <div>
    <h2>My Skills</h2>
    <Row gutter={[20, 20]}>
      <Col xs={24} sm={24} md={12}>

        <ProgressBar
          percent={90}
          text="Python"
        />
        <ProgressBar
          percent={85}
          text="C++"
        />
        <ProgressBar
          percent={80}
          text="Tensorflow"
        />
        <ProgressBar
          percent={90}
          text="OpenCV"
        />
      </Col>
      <Col xs={24} sm={24} md={12}>
        <ProgressBar
          percent={70}
          text="NodeJS"
        />
        <ProgressBar
          percent={90}
          text="HTML"
        />
        <ProgressBar
          percent={85}
          text="CSS"
        />
        <ProgressBar
          percent={75}
          text="MongoDB"
        />
      </Col>
    </Row>
  </div>
);

export default SkillsProgress;
