import React from 'react';
import { Row, Col } from 'antd';
import AboutTile from '../../AbouTile';
import { stripTags, domHtml } from '../../../utils/stripTags';

import SEO from '../../Seo';

const pageText = {
  paraOne: `Hey! My name is Rahul Rangarajan Kannan. I'm a final year student from Sri Venkateswara College of Engineering, Sriperumbudur. I'm passionate about Artificial Intelligence, Computer Vision and Web Development. I like to experiment with different Image processing projects and try to solve complex everyday problems using AI.`,
  paraTwo: `I'm always a learner and a self taught programmer.`,
};

const AboutMe = () => {
  const description = `${pageText.paraOne} ${stripTags(pageText.paraTwo)}`;
  return (
    <>
      <div>
        <SEO
          title="About"
          description={description}
          path=""
          keywords={['Rahul', 'RK', 'Machine Learning', 'Artificial Intelligence', 'OpenCV', 'Computer Vision', 'Student', 'Computer Science']}
        />
        <h1 className="titleSeparate">About Me</h1>
        <p>
          {pageText.paraOne}
        </p>
        <p dangerouslySetInnerHTML={domHtml(pageText.paraTwo)} />
      </div>
      <Row gutter={[20, 20]}>
      
        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="graduation.png"
            alt="graduation image"
            textH4="Pursuing B.Tech in"
            textH3="Information Technology"
            height={60}
            width={60}
          />
        </Col>

        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="web.png"
            alt="web image"
            textH4="Self Taught Programmer"
            textH3="Thanks to the Web Resources"
            height={60}
            width={60}
          />
        </Col>

        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="location.png"
            height={60}
            alt="location image"
            textH4="Born and bought up in"
            textH3="Chennai, TN, India"
          />
        </Col>

        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="game.png"
            alt="game image"
            textH4="Love Gaming"
            textH3="Gamer for life"
          />
        </Col>

        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="sher.png"
            alt="sherlock image"
            textH4="221B Baker street"
            textH3="Fan of Sherlock Holmes"
          />
        </Col>

        <Col xs={24} sm={24} md={12} lg={8}>
          <AboutTile
            img="elon.png"
            alt="elon image"
            textH4="The real-life Iron Man"
            textH3="Inspired by Elon Musk"
          />
        </Col>
 
      </Row>
    </>
  );
};
export default AboutMe;
